import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';
import Papa from 'papaparse';
import { useLanguage } from './LanguageProvider';


const DataContext = createContext();

export const useData = () => {
  const context = useContext(DataContext);
  if (!context) {
    throw new Error('useData doit être utilisé dans un DataProvider');
  }
  return context;
};

export const DataProvider = ({ children }) => {
  const [data, setData] = useState({
    publications: [],
    team: [],
    research: [],
    tools: [],
    news: [],
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const { language } = useLanguage();

  useEffect(() => {
    const fetchData = async () => {
      try {

        console.log('Starting data fetch...');
        
        const baseUrl = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRvv_lUiP6sK7A7QTxMI0eA3kllA9oj-D2Ee7BnCivHov_iKQ6p1CH96be-iMGi1_bDtMASdEC2Ibmi/pub?single=true&output=csv';

        const urls = {
          publications: language === 'fr' ? `${baseUrl}&gid=0` : `${baseUrl}&gid=0`,
          team: language === 'fr' ? `${baseUrl}&gid=1060799501` : `${baseUrl}&gid=1187176887`,
          research: language === 'fr' ? `${baseUrl}&gid=1203628962` : `${baseUrl}&gid=1137665444`,
          tools: language === 'fr' ? `${baseUrl}&gid=1187415274` : `${baseUrl}&gid=1307378646`,
          news: language === 'fr' ? `${baseUrl}&gid=1180969958` : `${baseUrl}&gid=1823112799`,
          contact: language === 'fr' ? `${baseUrl}&gid=634275759` : `${baseUrl}&gid=1211289998`
        };

        const [pubResponse, teamResponse, researchResponse, toolsResponse, newsResponse, contactResponse] = await Promise.all([
          axios.get(urls.publications),
          axios.get(urls.team),
          axios.get(urls.research),
          axios.get(urls.tools),
          axios.get(urls.news),
          axios.get(urls.contact)
        ]);

        console.log('All data fetched successfully');
        /*
        const parseData = (csvData) => {
          return new Promise((resolve) => {
            Papa.parse(csvData, {
              header: true,
              skipEmptyLines: true,
              complete: (results) => resolve(results.data)
            });
          });
        };
        */

        const parseData = (csvData) => {
          return new Promise((resolve) => {
            Papa.parse(csvData, {
              header: false,
              skipEmptyLines: true,
              complete: (results) => {
                
                const metadata = results.data[0];
                const sheetName = metadata[1];
        
                const dataRows = results.data.slice(1);
                const parsedData = Papa.parse(Papa.unparse(dataRows), {
                  header: true,
                  skipEmptyLines: true
                }).data;
        
                resolve({ sheetName, data: parsedData });
              }
            });
          });
        };



        const [publicationsResult, teamResult, researchResult, toolsResult, newsResult, contactResult] = await Promise.all([
          parseData(pubResponse.data),
          parseData(teamResponse.data),
          parseData(researchResponse.data),
          parseData(toolsResponse.data),
          parseData(newsResponse.data),
          parseData(contactResponse.data)
          
        ]);

        console.log('Publications count:', publicationsResult.data.length);
        console.log('Team members count:', teamResult.data.length);
        console.log('Research projects count:', researchResult.data.length);
        console.log('Tools count:', toolsResult.data.length);
        console.log('News count:', newsResult.data.length);
        console.log('Contact count:', contactResult.data.length);

        setData({
          publications: publicationsResult.data,
          team: teamResult.data,
          research: researchResult.data ,
          tools: toolsResult.data,
          news: newsResult.data,
          contact:  contactResult.data,
          sheetNames: {
            publications: publicationsResult.sheetName,
            team: teamResult.sheetName,
            research: researchResult.sheetName,
            tools: toolsResult.sheetName,
            news: newsResult.sheetName,
            contact: contactResult.sheetName
          }
        });
        setLoading(false);

      } catch (err) {
        console.error('Error details:', {
          message: err.message,
          response: err.response?.data,
          status: err.response?.status
        });
        setError(err);
        setLoading(false);
      }
    };

    fetchData();
  }, [language]);


  return (
    <DataContext.Provider value={{ data, loading, error }}>
      {children}
    </DataContext.Provider>
  );
};

export default DataProvider;