import React from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { Link, useLocation } from 'react-router-dom';
import { useData } from './DataLoader';
import LanguageToggleButton from './LanguageButton';
import './Header.css';





function Header() {
  const location = useLocation();
  const { data, loading, error} = useData();

  
  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>Error loading data</p>;
  }

  // Ensure data and sheetNames are defined
  const { sheetNames } = data;

  if (!sheetNames) {
    return <p>No data available</p>;
  }



  const navbarStyle = {
    boxShadow: '0 2px 4px rgba(0,0,0,.1)',
    padding: '1rem 0',
    position: 'relative',
  };

  const navLinkStyle = {
    fontSize: '1.1rem',
    position: 'relative',
    padding: '0.5rem 1rem',
  };

  const colorBarStyle = {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    height: '6px', 
    background: 'linear-gradient(to right, #0960fa 80%, #D5A00F 80%)',
  };

  return (
    <Navbar bg="light" expand="lg" style={navbarStyle}>
      <div style={colorBarStyle}></div>
      <Container>
        <Navbar.Brand as={Link} to="/" style={{ fontSize: '1.8rem' }}>Labo Langage</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto">
            <Nav.Link 
              as={Link} 
              to="/" 
              style={navLinkStyle} 
              className={`nav-item ${location.pathname === '/' ? 'active' : ''}`}
            >
              {data.sheetNames.news}
            </Nav.Link>
            <Nav.Link 
              as={Link} 
              to="/research" 
              style={navLinkStyle} 
              className={`nav-item ${location.pathname === '/research' ? 'active' : ''}`}
            >
              {data.sheetNames.research}
            </Nav.Link>
            <Nav.Link 
              as={Link} 
              to="/publications" 
              style={navLinkStyle} 
              className={`nav-item ${location.pathname === '/publications' ? 'active' : ''}`}
            >
              {data.sheetNames.publications}
            </Nav.Link>
            <Nav.Link 
              as={Link} 
              to="/team" 
              style={navLinkStyle} 
              className={`nav-item ${location.pathname === '/team' ? 'active' : ''}`}
            >
              {data.sheetNames.team}
            </Nav.Link>

            <Nav.Link 
              as={Link} 
              to="/tools" 
              style={navLinkStyle} 
              className={`nav-item ${location.pathname === '/tools' ? 'active' : ''}`}
            >
              {data.sheetNames.tools}
            </Nav.Link>

            <Nav.Link 
              as={Link} 
              to="/contact" 
              style={navLinkStyle} 
              className={`nav-item ${location.pathname === '/contact' ? 'active' : ''}`}
            >
              {data.sheetNames.contact}
            </Nav.Link>
          </Nav>

          <LanguageToggleButton />

        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Header;
